<template>
    <div>
        <grid :grid-data="data" :columns="columns" :showCheckbox="showCheckbox" :columnSet="columnSet" :left-height="20" @focus="focus" @updateValue="update" :showRate="true"></grid>
    </div>
</template>

<script>
import Grid from '../grid'

export default {
    components: {
        Grid,
    },
    data() {
        return {
            columnSet: true,
            showCheckbox: false,
            data: [],
            columns: [
                { title: '', key: 'brandName', width: 80 },
                { title: '', key: 'goodsName', width: 150 },
                { title: '', key: 'sn', width: 100 },
                { title: '', key: 'materialNo', width: 100 },
                { title: '', key: 'unit', width: 70 },
                { title: '', key: 'requiredQuantity', type: 'number', width: 70 },
                { title: '', key: 'customerRemarks', width: 150 },
                { title: '()', key: 'purchasePrice', type: 'number', width: 80 },
                { title: '()', key: 'salePrice', type: 'number', width: 80 },
                { title: '', width: 100, key: 'shipDesc' },
                {
                    title: '',
                    width: 70,
                    fixed: true,
                    renderButton(rowData, index) {
                        return [{
                            title: '',
                            click() {
                                console.log(rowData, index)  //eslint-disable-line
                            },
                        }]
                    },
                },
            ],
        }
    },
    created() {
        for (let i = 0; i < 100; i += 1) {
            this.data.push({
                brandName: `${i}`,
                goodsName: `${i}`,
                sn: `SDFSD${i}`,
                materialNo: `1231${i}`,
                unit: '',
                requiredQuantity: 10,
                customerRemarks: `${i}`,
                purchasePrice: 10.2,
                salePrice: 12.3,
                shipDesc: 10,
            })
        }
    },
    methods: {
        update(value) {
            console.log(value) //eslint-disable-line
        },
        focus(value) {
            console.log(value) //eslint-disable-line
        },
    },
}
</script>



